/** Media Queries **/
header {
  background-color: #000;
  text-align: center; }
  header .logo img {
    margin: 0 auto;
    width: 5%; }
  header ul {
    display: none; }
  header input:checked ~ ul {
    display: block; }
  header label {
    color: #fff;
    font-size: 4rem; }
  header input {
    display: none; }
  header ul {
    margin: 0 auto;
    padding: 0; }
    header ul li {
      margin: 2rem 0; }
      header ul li a {
        color: #fff; }

@media (min-width: 1024px) {
  header {
    align-items: center;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr); }
    header input {
      display: none; }
    header label {
      display: none; }
    header ul {
      display: grid;
      display: grid;
      gap: 0rem;
      grid-template-columns: repeat(4, 1fr); }
      header ul li a {
        transition: all 0.3s ease;
        border-bottom: 0.2rem solid transparent;
        padding-bottom: 1rem; }
      header ul li a:hover {
        border-bottom: 0.2rem solid #e7640d; } }

footer {
  text-align: center;
  padding: 3rem;
  background: #000;
  color: #fff; }
  footer .redes {
    text-align: center;
    margin-top: 2rem; }
    footer .redes .fa-facebook {
      color: #1877f2; }
    footer .redes .fa-whatsapp {
