    footer .redes .fa-instagram {
      color: #e1306c; }
    footer .redes a {
      color: #fff;
      font-size: 3rem;
      margin-right: 2rem; }

main {
  text-align: center; }

.direcciones {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem; }

.busqueda {
  font-size: 3rem;
  color: #e75f22;
  font-weight: bold; }

form .campo_toolpit {
  margin-bottom: 4rem; }

form .campo {
  display: grid;
  align-items: center;
  align-content: center;
  margin-bottom: 4rem; }

@media (min-width: 1024px) {
  .buscador {
    display: block; }
  form {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr); }
    form .campo_toolpit {
      display: grid;
      grid-template-columns: 20% 70% 10%;
      align-items: center;
      align-content: center; }
    form .campo {
      grid-template-columns: 20% 80%; } }

.radio {
  display: grid;
  grid-template-columns: repeat(4, 1fr); }

.valor {
